<template>
  <div class="notification-container">
    <Transition>
      <div v-show="alerting" class="alert-container">
        <h1>{{ $store.state.alertHeading }}</h1>
        <p>{{ $store.state.alertDescription }}</p>
      </div>
    </Transition>
  </div>
</template>

<script>
export default {
  name: "Notification",
  data() {
    return {
      alerting: false,
    };
  },
  computed: {
    alertId() {
      return this.$store.state.alertId;
    },
  },
  watch: {
    alertId() {
      this.triggerAlert();
    },
  },
  mounted() {
    // setInterval(() => {
    //   this.alerting = !this.alerting;
    // }, 1000);
  },
  methods: {
    triggerAlert() {
      this.alerting = true;
      setTimeout(() => {
        this.alerting = false;
      }, 1000);
    },
  },
};
</script>

